.wrapper {
	display: grid;
	grid-template-columns: 250px 1fr;
	gap: 30px;
}

.chat_messages {
	padding: 10px;
	max-height: 650px;
	overflow-y: auto;
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.chat {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 30px;
}

.addMessage {
	button {
		visibility: hidden;
	}
}
